@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
body {
  margin: 0;
  padding: 0;
  
}
* {
  box-sizing: border-box;
}
/* span-3d */
.span-3d{
  font-size: 150px;
  color:rgb(196, 193, 188);
  text-shadow: 0 0 2px #dfdfdf, 
      -4px 5px 0 #9a0808f7, 
      -7px 11px 0 #05637d,
      -12px 17px 0 #440f0f,
      -17px 23px 0 #000;
}
.span-3d2{
  font-size: 50px;
  color:rgb(196, 193, 188);
  text-shadow: 0 0 2px #dfdfdf, 
      -4px 2px 0 #9a0808f7, 
      -7px 7px 0 #05637d,
      -12px 10px 0 #440f0f,
      -17px 10px 0 #000;
}
/* contact-container */
.contact-container{
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: 80%;
}
/* row-container */
 /* Clear floats after the columns */
 .row-container:after {
  content: "";
  display: table;
  clear: both;
}

/* Popular courses */
.popular-hover:hover{
  padding: 5px;
  cursor: pointer;
  color: #dedada;
  }
  .caption-container{
    background-color: #f7fcfd;
    padding: 8px;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

/* Create three equal columns that floats next to each other */
.column-container {
  float: left;
  width: 33.33%;
  padding: 10px;

}

 /* Responsive layout - makes the three columns stack on top of each other instead of next to each other */
 @media screen and (max-width: 600px) {
  .column-container {
    width: 100%;
  }
}

/* top navbar */
.navBarTop {

  background-color: rgb(2, 2, 105);
}
/* Background Button */
