/*Newletter*/
.subscription.bg-white {
  background: none
}

.bg-white {
  background-color: #fff !important
}

.subscription.bg-white .subscription-wrapper {
  background: #fff
}

.subscription-wrapper {
  border-radius: 0% 5% 10% 3%/10% 20% 0% 17%;
  -webkit-transform: perspective(1800px) rotateY(20deg) skewY(1deg) translateX(50px);
  transform: perspective(1800px) rotateY(20deg) skewY(1deg) translateX(50px);
  padding: 70px 50px;
  z-index: 1;
  width: 100%;
  background: linear-gradient(80deg, #0030cc 0%, #00a4db 100%);
  position: relative;
  
}

.subscription-wrapper {
  box-shadow: 0px 15px 39px 0px rgba(8, 18, 109, 0.1) !important
}

.subscription-content {
  -webkit-transform: skewY(-1deg);
  transform: skewY(-1deg)
}


.subText {
  font-size: 30px;
  color: rgb(2, 2, 105);
}

.flex-fill {
  flex: 1 1 auto !important
}

.subscription.bg-white .form-control {
  border: 1px solid #ebebeb !important
}

.subscription-wrapper .form-control {
  height: 60px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 45px
}

.subscription-wrapper .form-control:focus {
  background: rgba(255, 255, 255, 0.1);
  outline: 0;
  box-shadow: none
}

.btnBgS:not(:disabled):not(.disabled) {
  cursor: pointer
}

.btnBgP {
  border: 0;
  color: #fff
}

.btnBgS {
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  text-transform: capitalize;
  padding: 18px 45px;
  border-radius: 45px;
  font-weight: 500;
  border: 1px solid;
  position: relative;
  z-index: 1;
  transition: .3s ease-in;
  overflow: hidden
}

.btnBgP:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 102%;
  height: 100%;
  background: linear-gradient(45deg, #00a8f4 0%, rgb(2, 2, 105) 100%);
  z-index: -1;
  transition: ease 0.3s
}

